form {
  margin: 4%;
  width:300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: #2D393A solid 6px;
  border-radius: 8px;
  background-color: #ababab;
  
  h2{
    margin-bottom: 2%;
    padding-left: 10%;
    padding-right: 10%;
    font-family: 'Fjalla One', sans-serif;
    font-size: 30px;
    color:#2D393A;
  }

  label{
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    flex-direction: column;
    padding-bottom: 2%;
  }

  button{
    margin-top: 4%;
    margin-bottom: 4%;
  }
  
}